
<template>
  <div  class="bg" style="position: relative;margin-top: 20%;">

    <section class="user">
      <img src="../assets/img/user.png" alt="" style="width:22%;">
         <input  type="tel" v-model="phone" auto-complete="off" style="border-bottom: 2px solid #1C1412;">
    </section>
    <section class="pwd" style="top: 66%;">
      <img src="../assets/img/pwd.png" alt="" style="width:22%;">
         <input  type="password" v-model="pass" auto-complete="off" style="border-bottom: 2px solid #1C1412;">
    </section>
    <img src="../assets/img/login2.png" style="position: absolute;bottom: -15%;left:0;right:0;margin-left:auto;margin-right:auto;width: 50%;z-index:1">
    <div @click="login"  style="position: absolute;z-index:2;background:transparent;bottom: -15.5%;left:0;right:0;margin-left:auto;margin-right:auto;width: 49%;height:25%;">

    </div>


    <p style="display:block;width:80%;height: 30px;line-height:30px;text-align:center;position: absolute;left:0;right:0;margin-left:auto;margin-right: auto;top:125%;font-size: 13px;color:black;">——VKA云会员提供技术服务——</p>
  </div>

</template>
<script>
import global from './Global.vue'
export default {
  name: 'heytealogin',
  data () {

    return {
      code: '',
      mess: '',
      phone:'',
      pass:'',
      storeId:'4617',
      ruleForm2: {
        name:'',
        pass: '',
        checkPass: '',
        age: ''
      },

    }
  },
  computed:{

  },
  created(){
    console.log(this.$route.query.openId)
    this.storeId = global.storeId
  },
  methods:{
    login(){
        var mobile=this.phone;
        var storeId=this.storeId;
        var password=this.pass;
        var openId=this.$route.query.openId;
        if(openId == undefined || openId == null || openId=='' ){
          openId = global.openId
        }
        var params={"mobile":mobile,"storeId":storeId,"password":password,"openId":openId};
        console.log(JSON.stringify(params));
        var myHeaders= new Headers();
        myHeaders.append('Content-Type','application/json');
        fetch(global.API+'/webapi/v1/card/activateToken', {
          method:'post',
          mode:'cors',
          headers:myHeaders,
          body:JSON.stringify(params)
        }).then(res => res.json()).then((d =>{

          console.log(d)
          if(d.code == '200'){
          global.cardId=d.cardId

            this.$router.push({
              name:'heytea',
              query: {
                cardId: d.cardId
              }
            })
          }
        }))
    //   var qs = require('qs');
    //   var json = encodeURI(JSON.stringify(params),"utf-8");
    //   console.log(json)
    //   this.$http.post('https://api.vi-ni.com/webapi/v1/card/activateToken',json
    //     {mode: 'cors',
    //       headers:myHeaders
    //     }).then(function(d){
    //     console.log(d.body)
    //       if(d.body.code == '200'){
    //         this.$router.push({
    //           name:'index',
    //           query: {
    //             cardId: d.body.cardId
    //           }
    //         })
    //       }
    //   }).catch(function (error) {
    //     alert('222')
    //     console.log(123)
    //     console.log(error);
    //   })
    },
    },

}
</script>
