    background-size: cover;
    background-repeat: no-repeat;
  }
section{
  width:56%;height: 5%;position: absolute;top:54%;left:0;right:0;margin-left:auto;margin-right:auto;padding-left:10px; display: flex;justify-content: space-between
}
  input{

    width:70%;height:100%;outline: none;
    border: none;
    font-size: 16px;
    background:transaction
  }


</style>




