<script src="../../bill/static/js/app.c5cc316bddf8772f9567.js"></script>
<template>
  <div class="bgbox" v-show=vshow>
       <section class="top">
         <img src="../assets/img/top.png" alt="">
       </section>
    <section class="middle" v-if="mess>0">
      <img src="../assets/img/left.png" alt="" style="position: absolute;top:50%;left:25px;width: 75px;height: 100px;">
      <img src="../assets/img/right.png" alt="" style="position: absolute;top:25%;right:10px;width: 100px;height: 80px;">
      <!--<img src="../assets/img/bottom.png" alt="" style="position: absolute;bottom:10%;right:50px;width: 110px;height: 120px;" @click="shareImg">-->
      <P>在刚刚过去的2017年<br>你一共在喜茶消费了<i>{{mess}}</i>元</P>
      <P>在 <i> {{address}}</i><br>你第一次踏上了喜茶星球</P>
      <P>你最常在<i>{{mostStore}}</i>喝茶</P>
      <P>更喜欢在<i>{{time}}</i>时段来杯喜茶<br>激发灵感</P>
      <p>在这一年里<br>喝茶足迹遍布<i>{{city}}</i>等城市</p>
      <p>你找到了自己最爱的那杯<i>{{tea}}</i><br>(购买了<i>{{times}}</i>次)<br>也渐渐学会了掌握生活的节奏</p>
      <p>懂得享受现在<br>也开始憧憬未来</p>
      <p>2018，愿我们一起变得更好</p>
    </section>
    <section class="middle" v-else-if="mess==0">
      <img src="../assets/img/left.png" alt="" style="position: absolute;top:50%;left:25px;width: 75px;height: 100px;">
      <img src="../assets/img/right.png" alt="" style="position: absolute;top:25%;right:10px;width: 100px;height: 80px;">
<!--       <img src="../assets/img/bottom.png" alt="" style="position: absolute;bottom:10%;right:50px;width: 110px;height: 120px;" @click="shareImg"> -->
      <P>2017，低调的你</P><p>没有在喜茶星球留下痕迹</p><p>2018，欢迎你来作客</P>
    </section>
  </div>
</template>

<script>
  var ImageUrl,openId,cardId;
  import global from './Global.vue'
  export default {
    name: 'heytea',
    data() {
      return {
        mess:'',
        address:'深圳益田广场',
        time:'下午茶',
        city:'广州、深圳',
        tea:'满杯红柚',
        times:456,
        mostStore:'',
        openId:'oTUIstzWnOqIAuMbWqcSIu2lMQRo',
        ImageUrl:'',
        storeId:'',
        cardId:'',
        vshow:false,
      }
    },
    created(){
      console.log(this.$route.query.cardId)
      // this.one()
//       console.log(global.openId)
      this.storeId= global.storeId

    },
    mounted(){
//       this.getImg()
    this.one()
    },
    methods: {
      getImg(){

        html2canvas(document.querySelector('#app'), {
          allowTaint: true,
          taintTest: false,
          onrendered: function(canvas) {
            console.log("runImg");
            canvas.id = "mycanvas";
            // document.body.appendChild(canvas);
            //生成base64图片数据
            var dataUrl = canvas.toDataURL();
            ImageUrl = dataUrl;

          }

        })
        // console.log(ImageUrl);
      },

      getUrlParam(paramName) {

         var url= location.search;
         var paramValue = "", isFound = !1;
         if (url.indexOf("?") == 0 && url.indexOf("=") > 1) {
            var arrSource = unescape(url).substring(1, url.length).split("&"), i = 0;
            while (i < arrSource.length && !isFound) arrSource[i].indexOf("=") > 0 && arrSource[i].split("=")[0].toLowerCase() == paramName.toLowerCase() && (paramValue = arrSource[i].split("=")[1], isFound = !0), i++
         }
         return paramValue == "" && (paramValue = null), paramValue
      },
      one() {
        if(global.openId==undefined || global.openId=='' || global.openId==null){
        var code = this.getUrlParam("code");
        var appid = this.getUrlParam("appid");
        var store = this.storeId;
    			console.log("appid:"+ appid);
          console.log("code:"+ code);
    var url=global.API+"/webapi/v1/weixin/weixin_user/"+appid+"/"+code+ "/"+store
        console.log("url:"+url );
        var myHeaders= new Headers();
        myHeaders.append('Content-Type','application/json');
        // fetch(url, {
        //   mode: 'cors',
        //   headers:myHeaders,
        // }).then(res => res.json()).then((d => {
        //     console.log(d)
        //     console.log(d.openId)
        //     openId = d.openId
        //     console.log(openId)
        //     console.log(cardId)
        //
        //     if(d.data.length==0){
        //       console.log(7587457865468548)
        //     this.$router.push({
        //       name:'heytealogin',
        //       query: {
        //         openId: d.openId
        //     }
        //     })
        //     }else{
        //       console.log(2324234324)
        //       cardId=d.data[0].card.id
        //       console.log(cardId)
        //       this.getdata()
        //     }
        //   }
        // ))
        this.$http.get(url, {
          mode: 'cors',
          headers:myHeaders,
          }).then(function(d){
              console.log(d.body)
              console.log(d.body.openId)
              openId = d.body.openId
              global.openId = openId
              console.log(openId)
              console.log(cardId)

              if(d.body.data.length==0){
                console.log(7587457865468548)
              this.$router.push({
                name:'heytealogin',
                query: {
                  openId: d.body.openId
              }
              })
              }else{
                console.log(2324234324)
                cardId=d.body.data[0].card.id
                global.cardId = cardId
                console.log(cardId)
                this.getdata()
              }
        }).catch(function (error) {

          console.log(123)
          console.log(error);
          // this.$router.push({
//                 name:'index',
//
//               })
        })
        }else{
          if(global.cardId == undefined || global.cardId == null || global.cardId == ''){

            console.log(global.cardId +"11111")
            this.$router.push({
                name:'heytealogin',
                query: {
                  openId: global.openId
              }
            })
          }else{
            openId=global.openId;
            cardId=global.cardId;
            this.getdata();
          }
        }
      },
      shareImg(){
        // console.log(11222333)
        // event.preventDefault();

        var newImg = document.createElement("img");
        newImg.src=ImageUrl;
        newImg.id='shareImg123';
        newImg.style='background:white;';
        document.querySelector(".bgbox").innerHTML=""
        document.querySelector('#app').appendChild(newImg);
        this.downloadIamge('#shareImg123','年度账单');
      },
      getdata(){
        console.log(openId,cardId)
        var storeId=this.storeId
        var params={"storeId":storeId,"cardId":cardId,"openId":openId};
        console.log(params)

        var myHeaders= new Headers();
        myHeaders.append('Content-Type','application/json');
        fetch(global.API+'/openapi/v4_2/reportForms/getCardAnnualReport', {
          method:'POST',
          mode: 'cors',
          headers:params,
        }).then(res => res.json()).then((d => {
            console.log(d)
          if(d.code==200){
              // mess:'5002.3',
              this.vshow=true,
              this.address=d.data.firstStoreName,
              this.mess=d.data.totalAmount,
              this.mostStore=d.data.mostStoreName,
              this.city=d.data.district,
              this.tea=d.data.mostProductName,
              this.times=d.data.mostProductNum
              if(d.data.mostTime==0){
                this.time='早上'
              }else if(d.data.mostTime==1){
                this.time='上午'
              }else if(d.data.mostTime==2){
                this.time='下午'
              }else if(d.data.mostTime==3){
                this.time='晚上'
              }
          }else if(d.code==1012){
            this.vshow=true,
            this.mess=0
          }else{
            this.$router.push({
                name:'heytealogin',
                query: {
                  openId: d.body.openId
              }
              })
          }
          }
        ))
        // this.$http.post('https://api.vi-ni.com/openapi/v4_2/reportForms/getCardAnnualReport',
//           {mode: 'cors',
//             headers:params,
//             method:'POST',
//           }).then(function(d){
//           console.log(d.body)
//             if(d.body.code==200){
//                 this.vshow=true,
//                 console.log(this.vshow)
//                 // mess:'5002.3',
//                 this.address=d.body.data.firstStoreName,
//                 this.mess=d.body.data.totalAmount,
//                 this.mostStore=d.body.data.mostStoreName,
//                 this.city=d.body.data.district,
//                 this.tea=d.body.data.mostProductName,
//                 this.times=d.body.data.mostProductNum
//                 if(d.body.data.mostTime==0){
//                   this.time='早上'
//                 }else if(d.body.data.mostTime==1){
//                   this.time='上午'
//                 }else if(d.body.data.mostTime==2){
//                   this.time='下午'
//                 }else if(d.body.data.mostTime==3){
//                   this.time='晚上'
//                 }
//             }else if(d.body.code==1012){
//               this.vshow=true,
//               this.mess=0
//             }
        // }).catch(function (error) {
//
//           console.log(123)
//           console.log(error);
//         })

      },
      downloadIamge(selector, name) {
    // 通过选择器获取img元素
    var img = document.querySelector(selector)
    // 将图片的src属性作为URL地址
    var url = img.src
    var a = document.createElement('a')
    var event = new MouseEvent('click')

    a.download = name || '年度账单'
    a.href = url

    a.dispatchEvent(event)
  }
  },

  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.top{
  width: 100%;
}
.top img{
  width: 100%;
}
  .middle{
    padding-top: 150px;
    padding-left: 90px;
    padding-right: 90px;
    position: relative;
    width: 100%;
    height: 1000px;
    background-image:url("data:image/png;base64,");
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }
  p{
    font-family: 'fz';
    margin-bottom: 15px;
    font-size: 12px;
  }
  i{
    font-style:normal;
    font-family: 'fzc';
    font-size: 14px;
    color: #E3C5A4;
  }

</style>



